import React, { useState, useEffect } from 'react';
import {
  Paper,
  TextField,
  Button,
  Grid,
  Typography,
  Alert,
  Box,
} from '@mui/material';
import { updateAbout } from '../../api';

const AboutForm = ({ data, onUpdate }) => {
  const [formData, setFormData] = useState({
    name: '',
    role: '',
    description: '',
    photo: '',
    resumeUrl: '',
    socialLinks: {
      github: '',
      linkedin: '',
      twitter: '',
    },
  });
  const [status, setStatus] = useState({ message: '', error: false });

  useEffect(() => {
    if (data) {
      setFormData(data);
    }
  }, [data]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await updateAbout(formData);
      setStatus({ message: 'About section updated successfully!', error: false });
      onUpdate();
    } catch (error) {
      setStatus({
        message: error.response?.data?.message || 'Error updating about section',
        error: true,
      });
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name.startsWith('social.')) {
      const social = name.split('.')[1];
      setFormData({
        ...formData,
        socialLinks: {
          ...formData.socialLinks,
          [social]: value,
        },
      });
    } else {
      setFormData({
        ...formData,
        [name]: value,
      });
    }
  };

  return (
    <Paper sx={{ p: 3 }}>
      <Typography variant="h6" gutterBottom>
        About Information
      </Typography>
      {status.message && (
        <Alert
          severity={status.error ? 'error' : 'success'}
          sx={{ mb: 2 }}
          onClose={() => setStatus({ message: '', error: false })}
        >
          {status.message}
        </Alert>
      )}
      <Box component="form" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              label="Name"
              name="name"
              value={formData.name}
              onChange={handleChange}
              required
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              label="Role"
              name="role"
              value={formData.role}
              onChange={handleChange}
              required
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Description"
              name="description"
              value={formData.description}
              onChange={handleChange}
              multiline
              rows={4}
              required
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              label="Photo URL"
              name="photo"
              value={formData.photo}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              label="Resume URL"
              name="resumeUrl"
              value={formData.resumeUrl}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              label="GitHub URL"
              name="social.github"
              value={formData.socialLinks.github}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              label="LinkedIn URL"
              name="social.linkedin"
              value={formData.socialLinks.linkedin}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              label="Twitter URL"
              name="social.twitter"
              value={formData.socialLinks.twitter}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12}>
            <Button type="submit" variant="contained" color="primary">
              Save Changes
            </Button>
          </Grid>
        </Grid>
      </Box>
    </Paper>
  );
};

export default AboutForm;
