import React, { useState, useEffect } from 'react';
import {
  Paper,
  TextField,
  Button,
  Grid,
  Typography,
  Alert,
  Box,
  IconButton,
  Card,
  CardContent,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import { updateExperience } from '../../api';

const ExperienceForm = ({ data, onUpdate }) => {
  const [experiences, setExperiences] = useState([]);
  const [status, setStatus] = useState({ message: '', error: false });

  useEffect(() => {
    if (data) {
      setExperiences(data);
    }
  }, [data]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await updateExperience(experiences);
      setStatus({ message: 'Experience updated successfully!', error: false });
      onUpdate();
    } catch (error) {
      setStatus({
        message: error.response?.data?.message || 'Error updating experience',
        error: true,
      });
    }
  };

  const handleAddExperience = () => {
    setExperiences([
      ...experiences,
      {
        company: '',
        role: '',
        duration: '',
        description: '',
      },
    ]);
  };

  const handleRemoveExperience = (index) => {
    setExperiences(experiences.filter((_, i) => i !== index));
  };

  const handleExperienceChange = (index, field, value) => {
    const updatedExperiences = [...experiences];
    updatedExperiences[index] = {
      ...updatedExperiences[index],
      [field]: value,
    };
    setExperiences(updatedExperiences);
  };

  return (
    <Paper sx={{ p: 3 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3 }}>
        <Typography variant="h6">Work Experience</Typography>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={handleAddExperience}
        >
          Add Experience
        </Button>
      </Box>
      {status.message && (
        <Alert
          severity={status.error ? 'error' : 'success'}
          sx={{ mb: 2 }}
          onClose={() => setStatus({ message: '', error: false })}
        >
          {status.message}
        </Alert>
      )}
      <Box component="form" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          {experiences.map((experience, index) => (
            <Grid item xs={12} key={index}>
              <Card>
                <CardContent>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                    <Typography variant="h6">Experience {index + 1}</Typography>
                    <IconButton
                      color="error"
                      onClick={() => handleRemoveExperience(index)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </Box>
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Company"
                        value={experience.company}
                        onChange={(e) =>
                          handleExperienceChange(index, 'company', e.target.value)
                        }
                        required
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Role"
                        value={experience.role}
                        onChange={(e) =>
                          handleExperienceChange(index, 'role', e.target.value)
                        }
                        required
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Duration"
                        value={experience.duration}
                        onChange={(e) =>
                          handleExperienceChange(index, 'duration', e.target.value)
                        }
                        placeholder="e.g., Jan 2020 - Present"
                        required
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        fullWidth
                        label="Description"
                        value={experience.description}
                        onChange={(e) =>
                          handleExperienceChange(
                            index,
                            'description',
                            e.target.value
                          )
                        }
                        multiline
                        rows={4}
                        required
                      />
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>
          ))}
          <Grid item xs={12}>
            <Button type="submit" variant="contained" color="primary">
              Save All Experience
            </Button>
          </Grid>
        </Grid>
      </Box>
    </Paper>
  );
};

export default ExperienceForm;
