import React, { useState, useEffect } from 'react';
import {
  Paper,
  TextField,
  Button,
  Grid,
  Typography,
  Alert,
  Box,
  IconButton,
  Card,
  CardContent,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import { updateEducation } from '../../api';

const EducationForm = ({ data, onUpdate }) => {
  const [education, setEducation] = useState([]);
  const [status, setStatus] = useState({ message: '', error: false });

  useEffect(() => {
    if (data) {
      setEducation(data);
    }
  }, [data]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await updateEducation(education);
      setStatus({ message: 'Education updated successfully!', error: false });
      onUpdate();
    } catch (error) {
      setStatus({
        message: error.response?.data?.message || 'Error updating education',
        error: true,
      });
    }
  };

  const handleAddEducation = () => {
    setEducation([
      ...education,
      {
        institution: '',
        degree: '',
        duration: '',
        description: '',
      },
    ]);
  };

  const handleRemoveEducation = (index) => {
    setEducation(education.filter((_, i) => i !== index));
  };

  const handleEducationChange = (index, field, value) => {
    const updatedEducation = [...education];
    updatedEducation[index] = {
      ...updatedEducation[index],
      [field]: value,
    };
    setEducation(updatedEducation);
  };

  return (
    <Paper sx={{ p: 3 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3 }}>
        <Typography variant="h6">Education</Typography>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={handleAddEducation}
        >
          Add Education
        </Button>
      </Box>
      {status.message && (
        <Alert
          severity={status.error ? 'error' : 'success'}
          sx={{ mb: 2 }}
          onClose={() => setStatus({ message: '', error: false })}
        >
          {status.message}
        </Alert>
      )}
      <Box component="form" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          {education.map((edu, index) => (
            <Grid item xs={12} key={index}>
              <Card>
                <CardContent>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                    <Typography variant="h6">Education {index + 1}</Typography>
                    <IconButton
                      color="error"
                      onClick={() => handleRemoveEducation(index)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </Box>
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Institution"
                        value={edu.institution}
                        onChange={(e) =>
                          handleEducationChange(
                            index,
                            'institution',
                            e.target.value
                          )
                        }
                        required
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Degree"
                        value={edu.degree}
                        onChange={(e) =>
                          handleEducationChange(index, 'degree', e.target.value)
                        }
                        required
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Duration"
                        value={edu.duration}
                        onChange={(e) =>
                          handleEducationChange(index, 'duration', e.target.value)
                        }
                        placeholder="e.g., 2018 - 2022"
                        required
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        fullWidth
                        label="Description"
                        value={edu.description}
                        onChange={(e) =>
                          handleEducationChange(
                            index,
                            'description',
                            e.target.value
                          )
                        }
                        multiline
                        rows={4}
                      />
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>
          ))}
          <Grid item xs={12}>
            <Button type="submit" variant="contained" color="primary">
              Save All Education
            </Button>
          </Grid>
        </Grid>
      </Box>
    </Paper>
  );
};

export default EducationForm;
