import React, { useState, useEffect } from 'react';
import {
  Paper,
  TextField,
  Button,
  Grid,
  Typography,
  Alert,
  Box,
  IconButton,
  Card,
  CardContent,
  Chip,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import { updateProjects } from '../../api';

const ProjectsForm = ({ data, onUpdate }) => {
  const [projects, setProjects] = useState([]);
  const [status, setStatus] = useState({ message: '', error: false });
  const [newTech, setNewTech] = useState('');

  useEffect(() => {
    if (data) {
      setProjects(data);
    }
  }, [data]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await updateProjects(projects);
      setStatus({ message: 'Projects updated successfully!', error: false });
      onUpdate();
    } catch (error) {
      setStatus({
        message: error.response?.data?.message || 'Error updating projects',
        error: true,
      });
    }
  };

  const handleAddProject = () => {
    setProjects([
      ...projects,
      {
        title: '',
        description: '',
        imageUrl: '',
        projectUrl: '',
        githubUrl: '',
        technologies: [],
      },
    ]);
  };

  const handleRemoveProject = (index) => {
    setProjects(projects.filter((_, i) => i !== index));
  };

  const handleProjectChange = (index, field, value) => {
    const updatedProjects = [...projects];
    updatedProjects[index] = {
      ...updatedProjects[index],
      [field]: value,
    };
    setProjects(updatedProjects);
  };

  const handleAddTech = (index) => {
    if (newTech.trim()) {
      const updatedProjects = [...projects];
      updatedProjects[index].technologies.push(newTech.trim());
      setProjects(updatedProjects);
      setNewTech('');
    }
  };

  const handleRemoveTech = (projectIndex, techIndex) => {
    const updatedProjects = [...projects];
    updatedProjects[projectIndex].technologies.splice(techIndex, 1);
    setProjects(updatedProjects);
  };

  return (
    <Paper sx={{ p: 3 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3 }}>
        <Typography variant="h6">Projects</Typography>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={handleAddProject}
        >
          Add Project
        </Button>
      </Box>
      {status.message && (
        <Alert
          severity={status.error ? 'error' : 'success'}
          sx={{ mb: 2 }}
          onClose={() => setStatus({ message: '', error: false })}
        >
          {status.message}
        </Alert>
      )}
      <Box component="form" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          {projects.map((project, index) => (
            <Grid item xs={12} key={index}>
              <Card>
                <CardContent>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                    <Typography variant="h6">Project {index + 1}</Typography>
                    <IconButton
                      color="error"
                      onClick={() => handleRemoveProject(index)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </Box>
                  <Grid container spacing={2}>
                    <Grid item xs={12}>
                      <TextField
                        fullWidth
                        label="Title"
                        value={project.title}
                        onChange={(e) =>
                          handleProjectChange(index, 'title', e.target.value)
                        }
                        required
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        fullWidth
                        label="Description"
                        value={project.description}
                        onChange={(e) =>
                          handleProjectChange(index, 'description', e.target.value)
                        }
                        multiline
                        rows={3}
                        required
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Image URL"
                        value={project.imageUrl}
                        onChange={(e) =>
                          handleProjectChange(index, 'imageUrl', e.target.value)
                        }
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Project URL"
                        value={project.projectUrl}
                        onChange={(e) =>
                          handleProjectChange(index, 'projectUrl', e.target.value)
                        }
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        fullWidth
                        label="GitHub URL"
                        value={project.githubUrl}
                        onChange={(e) =>
                          handleProjectChange(index, 'githubUrl', e.target.value)
                        }
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <Box sx={{ mb: 1 }}>
                        {project.technologies.map((tech, techIndex) => (
                          <Chip
                            key={techIndex}
                            label={tech}
                            onDelete={() => handleRemoveTech(index, techIndex)}
                            sx={{ mr: 1, mb: 1 }}
                          />
                        ))}
                      </Box>
                      <Box sx={{ display: 'flex', gap: 1 }}>
                        <TextField
                          label="Add Technology"
                          value={newTech}
                          onChange={(e) => setNewTech(e.target.value)}
                          onKeyPress={(e) => {
                            if (e.key === 'Enter') {
                              e.preventDefault();
                              handleAddTech(index);
                            }
                          }}
                        />
                        <Button
                          variant="outlined"
                          onClick={() => handleAddTech(index)}
                        >
                          Add
                        </Button>
                      </Box>
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>
          ))}
          <Grid item xs={12}>
            <Button type="submit" variant="contained" color="primary">
              Save All Projects
            </Button>
          </Grid>
        </Grid>
      </Box>
    </Paper>
  );
};

export default ProjectsForm;
