import React, { useState, useEffect } from 'react';
import {
  Container,
  Box,
  Tabs,
  Tab,
  Typography,
  Button,
  AppBar,
  Toolbar,
} from '@mui/material';
import AboutForm from '../components/admin/AboutForm';
import ProjectsForm from '../components/admin/ProjectsForm';
import ExperienceForm from '../components/admin/ExperienceForm';
import EducationForm from '../components/admin/EducationForm';
import SkillsForm from '../components/admin/SkillsForm';
import { useNavigate } from 'react-router-dom';
import { getPortfolio } from '../api';

const Admin = () => {
  const [tab, setTab] = useState(0);
  const [portfolioData, setPortfolioData] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    fetchPortfolioData();
  }, []);

  const fetchPortfolioData = async () => {
    try {
      const { data } = await getPortfolio();
      setPortfolioData(data);
    } catch (error) {
      console.error('Error fetching portfolio data:', error);
    }
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    navigate('/login');
  };

  const handleTabChange = (event, newValue) => {
    setTab(newValue);
  };

  const renderTabContent = () => {
    switch (tab) {
      case 0:
        return <AboutForm data={portfolioData?.about} onUpdate={fetchPortfolioData} />;
      case 1:
        return <ProjectsForm data={portfolioData?.projects} onUpdate={fetchPortfolioData} />;
      case 2:
        return <ExperienceForm data={portfolioData?.experience} onUpdate={fetchPortfolioData} />;
      case 3:
        return <EducationForm data={portfolioData?.education} onUpdate={fetchPortfolioData} />;
      case 4:
        return <SkillsForm data={portfolioData?.skills} onUpdate={fetchPortfolioData} />;
      default:
        return null;
    }
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Portfolio Admin
          </Typography>
          <Button color="inherit" onClick={handleLogout}>
            Logout
          </Button>
        </Toolbar>
      </AppBar>
      <Container maxWidth="lg" sx={{ mt: 4 }}>
        <Tabs
          value={tab}
          onChange={handleTabChange}
          variant="scrollable"
          scrollButtons="auto"
          sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}
        >
          <Tab label="About" />
          <Tab label="Projects" />
          <Tab label="Experience" />
          <Tab label="Education" />
          <Tab label="Skills" />
        </Tabs>
        <Box sx={{ py: 3 }}>{renderTabContent()}</Box>
      </Container>
    </Box>
  );
};

export default Admin;
